import { Backdrop, CircularProgress, LinearProgress } from "@material-ui/core";
import React, { useContext } from "react";
import { BrowserRouter as Router, Route, Redirect } from "react-router-dom";
import "./App.css";
import Content from "./Components/Content";
import GoalDetails from "./Components/GoalDetails";
import SignIn from "./Components/SignIn";
import SignUp from "./Components/SignUp";
import globalContext from "./context/global-context";
function App() {
  const { user, loading } = useContext(globalContext);

  function isLoggedIn() {
    if (user !== null) {
      return true;
    } else {
      return false;
    }
  }
  return (
    <div className="App">
      <Backdrop open={loading}>
        <CircularProgress color="inherit" />
      </Backdrop>
      <h1 className="title">Milestone Inventory</h1>
      {loading && <LinearProgress color="secondary" />}

      <Router>
        <Route
          exact
          path="/"
          render={() => (isLoggedIn() ? <Content /> : <Redirect to="/login" />)}
        />

        <Route
          path="/login"
          render={() => (isLoggedIn() ? <Redirect to="/" /> : <SignIn />)}
        />
        <Route
          exact
          path="/signUp"
          render={() => (isLoggedIn() ? <Redirect to="/" /> : <SignUp />)}
        />
        <Route path="/goals/:goalId" component={GoalDetails} />
      </Router>
    </div>
  );
}

export default App;
