import React, { useEffect, useReducer, useState } from "react";
import GlobalContext from "./global-context";
import goalReducer from "./goalReducer";
import firebase from "../firebase";
import * as goalActions from "./goalReducerActions";

function GlobalState(props) {
  let defaultgoals = [];
  const [goals, dispatch] = useReducer(goalReducer, defaultgoals);
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    authListener();
  }, []);
  const getData = async (u) => {
    const doc = await firebase.firestore().collection(`goals/${u.uid}/goals`);
    doc.onSnapshot(
      (docSnapshot) => {
        let res = docSnapshot.docs.map((d) => ({
          id: d.id,
          goal: d.data().goal,
          createdAt: d.data().createdAt,
          milestones: d.data().milestones,
          completed: d.data().completed,
        }));
        dispatch({
          type: goalActions.ADD_GOALS,
          goals: res,
          uid: u.uid,
        });
        setLoading(false);
      },
      (err) => {
        setLoading(false);
        console.log(`Encountered error: ${err}`);
      }
    );
  };
  function authListener() {
    setLoading(true);
    firebase.auth().onAuthStateChanged((u) => {
      if (u) {
        setUser(u);
        getData(u);
        setLoading(false);
      } else {
        setLoading(false);
        setUser(null);
      }
    });
  }
  return (
    <GlobalContext.Provider
      value={{
        goals,
        dispatch,
        user,
        setUser,
        loading,
        setLoading,
      }}
    >
      {props.children}
    </GlobalContext.Provider>
  );
}

export default GlobalState;
