import * as goalActions from "./goalReducerActions";
import firebase from "../firebase";

const db = firebase.firestore();

const goalReducer = (state, action) => {
  switch (action.type) {
    case goalActions.ADD_GOALS:
      console.log(action.uid);
      return action.goals;
    case goalActions.ADD_goal:
      let goalRef = db.collection(`goals/${action.user.uid}/goals`);

      if (state.length > 0) {
        goalRef
          .add(action.payload)
          .then((docRef) => {
            db.collection(`milestones`)
              .doc(`${docRef.id}`)
              .set(
                { uid: action.user.uid, milestones: action.milestones },
                { merge: true }
              );
            console.log("Milestone Added");
          })
          .catch((e) => console.log(e));
      } else {
        goalRef
          .add(action.payload)
          .then((docRef) => {
            db.collection(`milestones`)
              .doc(`${docRef.id}`)
              .set(
                { uid: action.user.uid, milestones: action.milestones },
                { merge: true }
              );
          })
          .catch((e) => console.log(e));
        db.collection(`goals`).doc(action.user.uid).set(
          {
            displayName: action.user.displayName,
            email: action.user.email,
            photoUrl: action.user.photoURL,
          },
          { merge: true }
        );
      }
      return state;
    case goalActions.DELETE_goal:
      db.collection("goals")
        .doc(action.payload.uid)
        .collection("goals")
        .doc(action.payload.goalId)
        .delete();

      db.collection("milestones").doc(action.payload.goalId).delete();
      return state;
    default:
      return state;
  }
};
export default goalReducer;
