import { Button, Divider, LinearProgress, Typography } from "@material-ui/core";
import { Delete } from "@material-ui/icons";
import React, { useContext } from "react";
import { Link } from "react-router-dom";
import globalContext from "../context/global-context";

function Goal({ goal, serial }) {
  const { dispatch, user } = useContext(globalContext);
  const deletegoal = (id) => {
    dispatch({ type: "DELETE_goal", payload: { goalId: id, uid: user.uid } });
  };
  let dateTime;
  // console.log(goal);
  if (goal.createdAt !== null) {
    var mydate = new Date(goal.createdAt.toDate());

    var month = [
      "January",
      "February",
      "March",
      "April",
      "May",
      "June",
      "July",
      "August",
      "September",
      "October",
      "November",
      "December",
    ][mydate.getMonth()];
    dateTime = `${mydate.getHours()}:${mydate.getMinutes()} - ${mydate.getDate()} ${month.substring(
      0,
      3
    )} ${mydate.getFullYear()}`;

    // d = new Date(goal.createdAt.toDate()).toString("MMMM YYYY");
    // console.log(d);
  }
  return (
    <div className="goal">
      <Divider />
      <div className="goal-contents">
        <Link to={`/goals/${goal.id}`}>
          <p>
            <span> {serial} -</span> &nbsp;{goal.goal}
          </p>
        </Link>
        <div style={{ display: "flex", alignItems: "center" }}>
          <Typography variant="caption" display="block">
            {goal.createdAt !== null && dateTime}
          </Typography>
          <Button style={{ color: "red" }} onClick={() => deletegoal(goal.id)}>
            <Delete color="secondary" />
          </Button>
        </div>
      </div>
      <LinearProgress
        variant="determinate"
        color="secondary"
        size={40}
        thickness={4}
        value={goal.completed}
      />
    </div>
  );
}

export default Goal;
