import firebase from "../firebase";
import { useHistory } from "react-router-dom";
import {
  Button,
  LinearProgress,
  makeStyles,
  Paper,
  Popover,
  TextField,
  Typography,
} from "@material-ui/core";
import { Add, CheckCircle, Delete, HighlightOff } from "@material-ui/icons";
import {
  Timeline,
  TimelineConnector,
  TimelineContent,
  TimelineDot,
  TimelineItem,
  TimelineOppositeContent,
  TimelineSeparator,
} from "@material-ui/lab";
import React, { useContext, useEffect, useState } from "react";
import globalContext from "../context/global-context";
import { v4 as uuidv4 } from "uuid";
const useStyles = makeStyles((theme) => ({
  paper: {
    padding: "6px 16px",
    display: "flex",
    justifyContent: "space-between",
    flexDirection: "column",
  },
  secondaryTail: {
    backgroundColor: theme.palette.secondary.main,
  },
}));
function GoalDetails({ match }) {
  const { goals } = useContext(globalContext);
  const [data, setData] = useState({});
  const [milestones, setMilestones] = useState([]);
  const [addMilestoneInput, setAddMilestoneInput] = useState("");
  const [anchorEl, setAnchorEl] = React.useState(false);
  const [milestoneIndexParam, setMilestoneIndexParam] = useState("");
  const classes = useStyles();

  const {
    params: { goalId },
  } = match;

  const db = firebase.firestore();
  useEffect(() => {
    if (goals.length > 0) {
      setData(goals.find((g) => g.id === goalId));
    }
    if (Object.keys(data).length > 0) {
      readMilestones({ id: data.id });
    }
  }, [goals, data]);

  const readMilestones = async ({ id }) => {
    let milestoneRef = db.collection(`milestones`).doc(id);
    milestoneRef.onSnapshot(
      (snap) => {
        setMilestones(
          snap.data().milestones.map((m) => {
            return { ...m };
          })
        );
      },
      (err) => {
        console.log(err);
      }
    );
  };

  let history = useHistory();
  function backButton() {
    history.push("/");
  }

  //POPOVER --------------

  const handleClose = () => {
    setAnchorEl(null);
  };

  const open = Boolean(anchorEl);
  const id = open ? "simple-popover" : undefined;

  const insertMilestonePopover = (e, param) => {
    e.preventDefault();

    setMilestoneIndexParam(param);
    setAnchorEl(e.currentTarget);
  };
  const addMilestone = (e) => {
    e.preventDefault();

    let newMilestones = milestones;

    let index = milestones.findIndex((i) => i.id === milestoneIndexParam);
    if (addMilestoneInput.length > 2) {
      if (milestoneIndexParam === -1) {
        newMilestones.push({
          id: uuidv4(),
          milestone: addMilestoneInput,
          createdAt: Date.now(),
          completed: false,
          completedAt: null,
        });
      } else {
        newMilestones.splice(index, 0, {
          id: uuidv4(),
          milestone: addMilestoneInput,
          createdAt: Date.now(),
          completed: false,
          completedAt: null,
        });
        db.collection("milestones")
          .doc(data.id)
          .set({ milestones: newMilestones });
      }
    }

    setAddMilestoneInput("");
    setAnchorEl(null);
  };

  const deleteMilestone = (e, id) => {
    e.preventDefault();
    let newMilestones = milestones;
    newMilestones = newMilestones.filter((ml) => ml.id !== id);
    db.collection("milestones").doc(data.id).set({ milestones: newMilestones });
  };
  const handleToggle = (e, id) => {
    e.preventDefault();
    let newMilestones = milestones;
    let index = milestones.findIndex((ml) => ml.id === id);
    if (newMilestones[index].completedAt === null) {
      newMilestones[index] = {
        ...newMilestones[index],
        completedAt: Date.now(),
      };
    } else {
      newMilestones[index] = { ...newMilestones[index], completedAt: null };
    }
    db.collection("milestones").doc(data.id).set({ milestones: newMilestones });
  };
  if (Object.keys(data).length === 0) {
    return (
      <div>
        <LinearProgress color="secondary" />
      </div>
    );
  }

  return (
    <div>
      <div className="goalDetail-header">
        <h1 style={{ color: "white" }}>{data.goal}</h1>
        <LinearProgress
          variant="determinate"
          color="secondary"
          size={40}
          thickness={20}
          value={data.completed}
        />
      </div>
      <Button onClick={backButton} color="primary" variant="contained">
        All Goals
      </Button>
      <Timeline align="alternate">
        {milestones.map((ml, i) => (
          <TimelineItem key={ml.id}>
            <TimelineOppositeContent></TimelineOppositeContent>
            <TimelineSeparator>
              <TimelineDot
                style={ml.completedAt ? { color: "green" } : { color: "red" }}
              >
                {i + 1}&nbsp;
                {ml.completedAt ? (
                  <CheckCircle />
                ) : (
                  <HighlightOff className="notCompletedMilestoneIcon" />
                )}
              </TimelineDot>
              <TimelineConnector />
            </TimelineSeparator>
            <TimelineContent>
              <Button onClick={(e) => insertMilestonePopover(e, ml.id)}>
                <Add /> Insert Milestone
              </Button>
              <Paper elevation={3} className={classes.paper}>
                <Typography variant="h6" component="h1">
                  {ml.milestone}
                </Typography>
                <Typography variant="body2" color="textSecondary">
                  {ml.completedAt && new Date(ml.completedAt).toLocaleString()}
                </Typography>
              </Paper>
              <div
                className="milestoneOptionsOpposite"
                style={{
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "space-between",
                }}
              >
                <div>
                  <Button
                    style={{ textAlign: "right" }}
                    onClick={(e) => handleToggle(e, ml.id)}
                  >
                    Toggle
                  </Button>
                  <Button
                    style={{ textAlign: "right" }}
                    onClick={(e) => deleteMilestone(e, ml.id)}
                  >
                    <Delete style={{ color: "pink" }} />
                  </Button>
                </div>
                <Typography variant="body2" color="textSecondary">
                  {new Date(ml.createdAt).toLocaleString()}
                </Typography>
              </div>
            </TimelineContent>
          </TimelineItem>
        ))}
        <Button onClick={(e) => insertMilestonePopover(e, -1)}>
          <Add /> Insert Milestone
        </Button>
      </Timeline>
      <Popover
        id={id}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "center",
        }}
        transformOrigin={{
          vertical: "top",
          horizontal: "center",
        }}
      >
        <form noValidate autoComplete="off">
          <TextField
            id="standard-basic"
            label="Add Milestone"
            value={addMilestoneInput}
            onChange={(e) => setAddMilestoneInput(e.target.value)}
          />
          <Button onClick={(e) => addMilestone(e)}>Add</Button>
        </form>
      </Popover>
    </div>
  );
}

export default GoalDetails;
