import React, { useContext, useState } from "react";
import {
  TextField,
  Typography,
  Button,
  CardContent,
  CardActions,
  Card,
  List,
  ListItem,
  ListItemText,
  Divider,
} from "@material-ui/core";
import firebase from "../firebase";
import { v4 as uuidv4 } from "uuid";
import globalContext from "../context/global-context";
import { Delete } from "@material-ui/icons";
function CreateGoal() {
  const { dispatch, user } = useContext(globalContext);
  const [goalInput, setgoalInput] = useState("");
  const [milestoneInput, setMilestoneInput] = useState("");
  const [milestones, setMilestones] = useState([]);
  const [toggleMilestoneDialog, setToggleMilestoneDialog] = useState(false);
  const addMilestone = (e) => {
    e.preventDefault();
    if (milestoneInput.length > 2) {
      setMilestones([
        ...milestones,
        {
          id: uuidv4(),
          milestone: milestoneInput,
          createdAt: Date.now(),
          completed: false,
          completedAt: null,
        },
      ]);
      setMilestoneInput("");
    }
  };
  const handleGo = async (e) => {
    e.preventDefault();
    if (goalInput.length > 2) {
      setToggleMilestoneDialog(!toggleMilestoneDialog);
    }
  };
  const fireButton = () => {
    dispatch({
      type: "ADD_goal",
      user: user,
      payload: {
        goal: goalInput,
        createdAt: firebase.firestore.FieldValue.serverTimestamp(),
        completed: 0,
      },
      milestones,
    });
    setToggleMilestoneDialog(false);
    setgoalInput("");
    setMilestones([]);
  };
  return (
    <div>
      <form autoComplete="off">
        <Card>
          <CardContent>
            <Typography color="textSecondary" gutterBottom>
              Let's plan for a better future !
            </Typography>
          </CardContent>
          <CardActions>
            <TextField
              fullWidth
              required
              id="filled-required"
              label="Let's do it! ðŸ˜‰"
              placeholder="Enter the goal ðŸŽ¯"
              value={goalInput}
              onChange={(e) => setgoalInput(e.target.value)}
              // variant="filled"
            />
            <Button
              variant="contained"
              color="secondary"
              type="submit"
              onClick={handleGo}
            >
              GO!
            </Button>
          </CardActions>
          {toggleMilestoneDialog && (
            <Card>
              <CardContent>
                <Typography color="textSecondary" gutterBottom>
                  Create Milestones for the goal
                </Typography>
                <TextField
                  required
                  id="filled-required"
                  label="Milestone"
                  placeholder="The Milestone."
                  value={milestoneInput}
                  onChange={(e) => setMilestoneInput(e.target.value)}
                  // variant="filled"
                />
                <Button
                  variant="contained"
                  color="secondary"
                  type="submit"
                  onClick={addMilestone}
                >
                  Add Milestone!
                </Button>
                {milestones.length > 0 && (
                  <div>
                    <List>
                      {milestones.map((m, key) => (
                        <ListItem key={key} divider>
                          <Divider />
                          <Button
                            onClick={() => {
                              setMilestones(
                                milestones.filter(
                                  (milestone) => milestone !== m
                                )
                              );
                            }}
                          >
                            <Delete color="secondary" />
                          </Button>
                          {key + 1} &emsp;
                          <ListItemText primary={m.milestone} />
                        </ListItem>
                        // <li>{m.milestone}</li>
                      ))}
                    </List>

                    <Button color="secondary" onClick={fireButton}>
                      Fire!
                    </Button>
                  </div>
                )}
              </CardContent>
            </Card>
          )}
        </Card>
      </form>
    </div>
  );
}

export default CreateGoal;
